.page {
  width: 100%;
  flex-flow: nowrap;
  display: flex;
  padding: 0 36px;
  box-sizing: border-box;
}

h3.title {
  margin-bottom: 0px;
}

.description {
  margin-bottom: 24px;
}

.publishRow {
  width: 100%;
  flex-flow: wrap;
  display: flex;
  align-items: center;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 6px 0 12px;
}

.leftCol {
  flex: 1 1 auto;
  min-width: 700px;
}

.preview {
  max-width: 1000px;
  padding: 48px 0 0 12px;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.centerText {
  text-align: center;
}

.tagsChipHeader {
  margin-top: 24px;
  width: 100%;
  flex-flow: nowrap;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  padding-bottom: 4px;
}

.tagChipsInput {
  width: 100%;
  margin-bottom: 36px;
}

.actionBlock {
  display: flex;
  flex-direction: column;
  gap: 8px;
}